<template>
    <div class="table-wrapper">
        <div
            class="table"
            @click="showResult = !showResult"
        >
            <div
                v-for="i in 5"
                :key="i+10"
                class="table-button left"
            >
                <div
                    class="tile-item bg-yellow"
                    :class="{selected: this.term === i}"
                    @click="(e) => {e.stopPropagation(); this.term = i}"
                >
                    {{ i }}
                </div>
            </div>
            <div
                v-for="i in 10"
                :key="i"
                class="table-item"
            >
                {{ term }} &#215; {{ i }} =&nbsp; 
                <span
                    v-if="showResult"
                >
                    {{ term*i }}
                </span>
            </div>
            <div
                v-for="i in 5"
                :key="i+15"
                class="table-button right"
            >
                <div
                    class="tile-item bg-yellow"
                    :class="{selected: this.term === i+5}"
                    @click="(e) => {e.stopPropagation(); this.term = i+5}"
                >
                    {{ i+5 }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: [ 'term1', 'term2', 'sign' ],
    data() {
        return {
            term: this.sign === 'multiply' ? this.term1 : this.term2,
            showResult: true,
        }
    }
}
</script>

